import * as React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import Seo from "../components/seo"
import { ContentPage, Title } from "../styles"

const PrivacyPolicy = () => (
  <Layout>
    <Seo title="Privacy Policy" />
    <ContentPage className="content-page">

      <Title className="content-page--heading" as="h2">{`Privacy Policy`}</Title>
      <p>At Evoytic, accessible from https://evoytic.com/, one of our main priorities is the privacy of our visitors. This Privacy Policy document contains types of information that is collected and recorded by Evoytic and how we use it.</p>
      <p>If you have additional questions or require more information about our Privacy Policy, do not hesitate to contact us. <b/><b/>This Privacy Policy applies only to our online activities and is valid for visitors to our website with regards to the information that they shared and/or collect in Evoytic. This policy is not applicable to any information collected offline or via channels other than this website. Our Privacy Policy was created with the help of the Privacy Policy Generator.</p>

      <Title className="content-title" as="h3" option={{ color: 'rgba(0, 0, 0, 1)', size: '2rem', weight: '400'}}>{`Consent`}</Title>
      <p>By using our website, you hereby consent to our Privacy Policy and agree to its terms.</p>

      <Title className="content-title" as="h3" option={{ color: 'rgba(0, 0, 0, 1)', size: '2rem', weight: '400'}}>{`Information we collect`}</Title>
      <p>The personal information that you are asked to provide, and the reasons why you are asked to provide it, will be made clear to you at the point we ask you to provide your personal information. </p>
      <p>If you contact us directly, we may receive additional information about you such as your name, email address, phone number, the contents of the message and/or attachments you may send us, and any other information you may choose to provide. </p>
      <p>When you register for an Account, we may ask for your contact information, including items such as name, company name, address, email address, and telephone number.</p>

      <Title className="content-title" as="h3" option={{ color: 'rgba(0, 0, 0, 1)', size: '2rem', weight: '400'}}>How we use your information</Title>
      <p>We use the information we collect in various ways, including to:</p>

      <ul className="content-unlist">
        <li className="content-unlist--item">Provide, operate, and maintain our website</li>
        <li className="content-unlist--item">Improve, personalize, and expand our website</li>
        <li className="content-unlist--item">Understand and analyze how you use our website</li>
        <li className="content-unlist--item">Develop new products, services, features, and functionality</li>
        <li className="content-unlist--item">Communicate with you, either directly or through one of our partners, including for customer service, to provide you with updates and other information relating to the website, and for marketing and promotional purposes</li>
        <li className="content-unlist--item">Send you emails</li>
        <li className="content-unlist--item">Find and prevent fraud</li>
      </ul>

      <Title className="content-title" as="h3" option={{ color: 'rgba(0, 0, 0, 1)', size: '2rem', weight: '400'}}>{`Log Files`}</Title>
      <p>Evoytic follows a standard procedure of using log files. These files log visitors when they visit websites. All hosting companies do this and a part of hosting services analytics. The information collected by log files include internet protocol (IP) addresses, browser type, Internet Service Provider (ISP), date and time stamp, referring/exit pages, and possibly the number of clicks. These are not linked to any information that is personally identifiable. The purpose of the information is for analyzing trends, administering the site, tracking users movement on the website, and gathering demographic information.</p>

      <Title className="content-title" as="h3" option={{ color: 'rgba(0, 0, 0, 1)', size: '2rem', weight: '400'}}>Cookies and Web Beacons</Title>
      <p>Like any other website, Evoytic uses <strong>cookies</strong>. These cookies are used to store information including visitors preferences, and the pages on the website that the visitor accessed or visited. The information is used to optimize the users experience by customizing our web page content based on visitors browser type and/or other information. </p>
      <p>For more general information on cookies, please read <Link to="https://www.privacypolicyonline.com/what-are-cookies/">&quot;What Are Cookies&quot;</Link> from Cookie Consent.</p>
      
      <Title className="content-title" as="h3" option={{ color: 'rgba(0, 0, 0, 1)', size: '2rem', weight: '400'}}>{`Advertising Partners Privacy Policies`}</Title>
      <p>You may consult this list to find the Privacy Policy for each of the advertising partners of Evoytic.</p>
      <p>Third-party ad servers or ad networks uses technologies like cookies, JavaScript, or Web Beacons that are used in their respective advertisements and links that appear on Evoytic, which are sent directly to users browser. They automatically receive your IP address when this occurs. These technologies are used to measure the effectiveness of their advertising campaigns and/or to personalize the advertising content that you see on websites that you visit.</p>
      <p>Note that Evoytic has no access to or control over these cookies that are used by third-party advertisers.</p>

      <Title className="content-title" as="h3" option={{ color: 'rgba(0, 0, 0, 1)', size: '2rem', weight: '400'}}>{`Third Party Privacy Policies`}</Title>
      <p>Evoytic's Privacy Policy does not apply to other advertisers or websites. Thus, we are advising you to consult the respective Privacy Policies of these third-party ad servers for more detailed information. It may include their practices and instructions about how to opt-out of certain options. </p>
      <p>You can choose to disable cookies through your individual browser options. To know more detailed information about cookie management with specific web browsers, it can be found at the browsers respective websites.</p>

      <Title className="content-title" as="h3" option={{ color: 'rgba(0, 0, 0, 1)', size: '2rem', weight: '400'}}>{`CCPA Privacy Rights (Do Not Sell My Personal Information)`}</Title>
      <p>Under the CCPA, among other rights, California consumers have the right to: Request that a business that collects a consumer's personal data disclose the categories and specific pieces of personal data that a business has collected about consumers.</p>
      <p> Request that a business delete any personal data about the consumer that a business has collected.</p>
      <p>Request that a business that sells a consumer's personal data, not sell the consumer's personal data.</p>
      <p>If you make a request, we have one month to respond to you. If you would like to exercise any of these rights, please contact us.</p>

      <Title className="content-title" as="h3" option={{ color: 'rgba(0, 0, 0, 1)', size: '2rem', weight: '400'}}>{`GDPR Data Protection Rights`}</Title>
      <p>We would like to make sure you are fully aware of all of your data protection rights. Every user
      is entitled to the following:</p>
      
      <p><strong>The right to access – </strong> You have the right to request copies of your personal data. We may charge you a small fee for this service.</p>
      <p><strong>The right to rectification – </strong> You have the right to request that we correct any information you believe is inaccurate. You also have the right to request that we complete the information you believe is incomplete.</p>
      <p><strong>The right to erasure – </strong> You have the right to request that we erase your personal data, under certain conditions.</p>
      <p><strong>The right to restrict processing – </strong> You have the right to request that we restrict the processing of your personal data, under certain conditions.</p>
      <p><strong>The right to object to processing – </strong> You have the right to object to our processing of your personal data, under certain conditions.</p>
      <p><strong>The right to data portability – </strong> You have the right to request that we transfer the data that we have collected to another organization, or directly to you, under certain conditions.</p>
      <p>If you make a request, we have one month to respond to you. If you would like to exercise any of these rights, please contact us.</p>
 
      <Title className="content-title" as="h3" option={{ color: 'rgba(0, 0, 0, 1)', size: '2rem', weight: '400'}}>{`Children's Information`}</Title>
      <p>Another part of our priority is adding protection for children while using the internet. We encourage parents and guardians to observe, participate in, and/or monitor and guide their online activity.</p>
      <p>Evoytic does not knowingly collect any Personal Identifiable Information from children under the age of 13. If you think that your child provided this kind of information on our website, we strongly encourage you to contact us immediately and we will do our best efforts to promptly remove such information from our records.</p>
    </ContentPage>
  </Layout>
)

export default PrivacyPolicy
